body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  color: orange;
  font-size: 18px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.map {
  border: solid 1px orange;
  margin-top: 10px;
  color: black;
  width: 70vw;
  height: 40vh;
}

.map > div:first-child {
  position: relative !important;
}

.content {
  margin: auto;
  width: 100%;
  height: 100%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.placesList, .keywordsList {
  z-index: 1;
  position: relative;
}

.placeListTitle:hover, .keywordListTitle:hover {
  color: gold !important;
  cursor: pointer;
}

.placeListTitle, .keywordListTitle {
  color: orange;
  font-weight: bold;
}

.placeTitle:hover, .keywordTitle:hover {
  cursor: pointer;
}

.placeListObject, .keywordListObject {
  margin-top: 5px;
  padding: 0 5px 5px 5px;
  clear: both;
  overflow: hidden;
}

.linearGradient {
  background-image: -webkit-gradient(linear, right top, left top, from(#282828) , to(#000000));
  background-image: -webkit-linear-gradient(right, #282828 , #000000);
  background-image: linear-gradient(to left, #282828 , #000000);
}

.goldenButton {
  background-color: gold !important;
}

.keywordInfo {
  margin-bottom: 10px;
}
.createPlaceForm, .createKeywordForm {
    margin-top: 10px;
    margin-bottom: 20px;
    z-index: 1;
    position: relative;
}

.updatePlaceForm, .updateKeywordForm, .createKeywordForm {
    margin-top: 10px;
    margin-bottom: 10px;
    float: right;
    clear: both;
}

.createPlaceLabel, .updatePlaceLabel,
.createKeywordLabel, .updateKeywordLabel {
    float: left;
}
  
.createPlaceInput, .updatePlaceInput,
.createKeywordInput, .updateKeywordInput {
    display: block;
    margin-left: 15vw;
    background-color: orange;
    border: none;
    border-radius: 5px;
    padding: 5px;
}

.createPlaceInput:focus, .updatePlaceInput:focus,
.createKeywordInput:focus, .updateKeywordInput:focus {
    background-color: gold !important;
}
  
.addPlaceButton,
.createPlaceSaveButton,
.deletePlaceButton,
.updatePlaceButton,
.updatePlaceSaveButton,
.addKeywordButton,
.createKeywordSaveButton,
.deleteKeywordButton,
.updateKeywordButton,
.updateKeywordSaveButton {
    background-color: orange;
    cursor: pointer;
    padding: 5px;
    border: none;
    border-radius: 3px;
    font-size: 18px;
    margin-top: 5px;
}

.addPlaceButton:hover,
.createPlaceSaveButton:hover,
.deletePlaceButton:hover,
.updatePlaceButton:hover,
.updatePlaceSaveButton:hover,
.addKeywordButton:hover,
.createKeywordSaveButton:hover,
.deleteKeywordButton:hover,
.updateKeywordButton:hover,
.updateKeywordSaveButton:hover {
    background-color: gold;
}

.deletePlaceButton,
.deleteKeywordButton {
    margin-top: 5px;
    margin-right: 5px;
}

.updatePlaceButton,
.updateKeywordButton {
    margin-top: 5px;
    margin-left: 5px;
}

.updatePlaceSaveButton,
.updateKeywordSaveButton {
    margin-top: 10px;
    margin-left: 150px;
}

.createPlaceSaveButton,
.createKeywordSaveButton {
    margin-top: 10px;
}

.createPlaceFormRow, .updatePlaceFormRow,
.createKeywordFormRow, .updateKeywordFormRow {
    margin-top: 5px;
}

.placeButtons {
    clear: both;
}

.addKeywordButton {
    float: right;
    clear: both;
}
#alignedDivs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

#leftDiv {
    flex: 0 0 auto;
}

#rightDiv {
    flex: 1 1;
    text-align: right;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
}
@media only screen and (max-width: 800px) {
    #alignedDivs {
        display: block;
        flex-direction: unset;
        justify-content: unset;
        min-width: 100%;
        width: -webkit-min-content;
        width: -moz-min-content;
        width: min-content;
    }
    
    #leftDiv {
        flex: none;
        display: block;
    }
    
    #rightDiv {
        display: block;
        flex: none;
        text-align: left;
        width: auto;
    }

    .updatePlaceForm {
        margin-top: 5px;
        float: none;
        clear: none;
    }

    .linearGradient {
        background-image: -webkit-gradient(linear, left top, right top, from(#282828) , to(#000000));
        background-image: -webkit-linear-gradient(left, #282828 , #000000);
        background-image: linear-gradient(to right, #282828 , #000000);
    }

    .createPlaceInput, .updatePlaceInput {
        margin-left: 150px;
    }
}

@media only screen and (max-width: 450px) {
    .createPlaceLabel, .updatePlaceLabel {
        float: none;
        width: 100%;
    }

    .createPlaceInput, .updatePlaceInput {
        margin-left: 0;
        width: 100%;
    }

    .createPlaceSaveButton, .updatePlaceSaveButton {
        margin-left: 0;
    }
}
